.choose__breed__wrap {
  background: url("/images/home/2.png");
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 200px) minmax(0, 200px) minmax(0, auto);
  padding: 70px 50px;
  grid-gap: 50px;
}

.choose__breed__wrap:after {
  position: absolute;
  content: '';
  left:0; top:0;
  width: 100%;
  height: 100%;
  background-color: rgba(195, 147, 108, 0.57);
}


.choose__breed__left {
  z-index: 1;
  font-size:48px;
  color: #deffa8;
  font-family: montserrat,sans-serif;
  font-weight: 400;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  text-transform: uppercase;
}

.choose__breed__left__first {
  margin-bottom: 40px;
}

.choose__breed__left__second {
  position: relative;
  writing-mode: tb-rl;
  transform:rotate(180deg);
}

.choose__breed__right {
  color: #fff;
  z-index: 1;
  font-family: roboto,sans-serif;
  background-color: rgba(48, 46, 42, 0.6);
  padding: 10px;
  text-align: center;
  border-radius: 70px;
}

.choose__breed__right__title {
  font-size: 26px;
  font-family: montserrat,sans-serif;
  font-weight: 400;
  padding: 30px 0;
  margin-top: 80px;
}

.choose__breed__right__title:first-child {
  margin-top: 0;
}

.choose__breed__right__text {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.7;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  font-family: montserrat,sans-serif;
}

@media (max-width: 768px) {
  .choose__breed__wrap {
    background-position: top right;
    background-size: 100%;
    background-repeat: no-repeat;
    grid-template-columns: 1fr;
    padding: 10px;
    background-attachment: unset;
  }

  .choose__breed__left__first .circle {
    margin: 0 auto;
  }

  .choose__breed__left__second {
      position: relative;
      writing-mode: unset;
      transform: unset;
      text-align: center;
  }
}

@media (max-width: 512px) {
  .choose__breed__wrap {
    background-position: top right;
    background-attachment: unset;
  }
}