.advantages__title {
  @include visually-hidden;
}

.advantages__wrap {
  background-size: cover !important;
  background-attachment: fixed;
  background-position: center center !important;
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 200px) minmax(0, auto);
  grid-gap: 50px;
  padding: 150px 50px;
  width: 100%;
}

.advantages__left {
  z-index: 1;
  font-size:48px;
  color: #a2a2a2;
  font-family: montserrat,sans-serif;
  font-weight: 400;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  text-transform: uppercase;
}

.advantages__left__first {
  margin-bottom: 40px;
}

.advantages__left__second {
  position: relative;
  writing-mode: tb-rl;
  transform:rotate(180deg);
}


.advantages__list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, auto));
  grid-column-gap: 30px;
}

.advantages__item{
  text-align: center;

  &:hover {
    .advantages__img {
      border-color: $blue;
    }

    .advantages__svg {
      fill: $blue;
    }

    .advantages__subtitle{
      color: $blue;
    }

    .advantages__text{
      color: $black;
    }
  }
}


.advantages__img {
  border-style: solid;
  border-width: 1px;
  border-color: $grey;
  border-radius: 50%;
  position: relative;
  width: 104px;
  height: 104px;
  transition-duration: 500ms;
  transition-property: border-color;
  margin: 0 auto;
}

.advantages__svg {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 40px;
  fill: $grey;
  transform: translate(-50%, -50%);
  transition-duration: 500ms;
  transition-property: fill;
  width: 100%;
}

.advantages__subtitle {
  font-size: 30px;
  line-height: 1.13333;
  color: #372d2d;
  text-transform: uppercase;
}

.advantages__text {
  font-family: PT Sans,Helvetica,Arial,sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.7;
  word-spacing: normal;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  color: #858585;
}

@media (max-width: 768px) {
  .advantages__wrap {
    grid-template-columns: 1fr;
  }

  .advantages__left__first .circle {
    margin: 0 auto;
  }

  .advantages__left__second {
    position: relative;
    writing-mode: unset;
    transform: unset;
    text-align: center;
  }

  .advantages__list {
    grid-template-columns: 1fr;
  }

}

@media (max-width: 512px) {

  .advantages__wrap {
    padding: 10px;
  }

  .advantages__item {
    text-align: center;
  }

  .advantages__img {

  }

  .advantages__list {
    padding-left: 0;
  }

  .advantages__left {
    text-align: center;
  }

}
