$lineColor: #2f2f2f;
$photosText: #464646;
$descriptionColor: #ffffff;
$borderColor: white;
$fillColor: rgba(134, 179, 210, 0.8);

.about__header {
  align-self: center;
  justify-self: center;
  margin-right: 20px;
  margin-top: 0;
}

.photos__header__top {
  z-index: 1;
  position: relative;
  margin-top: 20px;
  display: block;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 15px;
  color: #00a8e0;
  letter-spacing: 4px;
  text-shadow: 2px 2px 2px rgba(41, 41, 41, 0.26);
  font-family: 'Open Sans', sans-serif;
}

.photos__wrap {
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 200px)  minmax(0, auto);
  padding: 150px 50px;
  grid-gap: 50px;
}

.photos__wrap:after {
  position: absolute;
  content: '';
  left:0; top:0;
  width: 100%;
  height: 100%;
  background-color: #ecf4f3;
}


.photos__left {
  z-index: 1;
  font-size:48px;
  color: #a2a2a2;
  font-family: montserrat,sans-serif;
  font-weight: 400;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  text-transform: uppercase;
}

.photos__left__first {
  margin-bottom: 40px;
}

.photos__left__second {
  position: relative;
  writing-mode: tb-rl;
  transform:rotate(180deg);
}


.photos__header__bottom {
  text-shadow: 2px 2px 2px rgba(41, 41, 41, 0.26);
  font-size: 25px;
  color: #2b2b2b;
  font-weight: 800;
  text-transform: uppercase;
  display: block;
  text-align: center;
  letter-spacing: 1px;
  z-index: 1;
  position: relative;
}

.photos__list {
  padding-bottom: 50px;
  padding-right: 40px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(12, minmax(0px, auto));
  grid-auto-rows: minmax(270px, auto);
}

.photos__under__text {
  font-family: PT Sans,Helvetica,Arial,sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.7;
  word-spacing: normal;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  color: #858585;
  z-index: 1;
  position: relative;
  padding-left: 10px;
  padding-right: 10px;
}

.photos__element--three    {
  grid-column: span 3;
}

.photos__element--four {
  grid-column: span 4;
}

.photos__element--five {
  grid-column: span 5;
}

.photos__element--six {
  grid-column: span 6;
}

.photos__title {
  font-size: 30px;
  line-height: 1.13333;
  text-transform: uppercase;
  color: #858585;
  font-weight: 800;
  margin: 25px 0;
  z-index: 1;
  position: relative;

  &::before {
    background-color: #fff;
    position: absolute;
    width: 70px;
    height: 2px;
    left: 0;
    top: -25px;
    content: '';
  }

}

.photos__text {
  font-family: PT Sans,Helvetica,Arial,sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.7;
  word-spacing: normal;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  color: $white;
  z-index: 1;
  position: relative;
}

.photos__element {
  z-index: 1;
  position: relative;

  &:first-child {
    max-width: 270px;

    &::before {
      display: none;
    }

  }

  &::before {
    background-color: $fillColor;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0;
    content: '';
    transition-property: opacity;
    transition-duration: 500ms;
  }

  &:hover {

    &::before {
      z-index: 1;
      opacity: 1;
    }

    .photos__info {
      border-color: $borderColor;
      opacity: 1;
      z-index: 2;
    }

  }


