    background-size: cover;
  }
  
    &:nth-child(8) {
    background: url('/images/photogallery/g8.jpg') center no-repeat;
    background-size: cover;
  }
  
      &:nth-child(9) {
    background: url('/images/photogallery/g9.jpg') center no-repeat;
    background-size: cover;
  }
  
      &:nth-child(10) {
    background: url('/images/photogallery/g10.jpg') center no-repeat;
    background-size: cover;
  }
  
      &:nth-child(11) {
