.photos__info {
  width: 100%;
  height: 100%;
  position: relative;
  border: 5px solid transparent;
  box-sizing: border-box;
  z-index: -1;
  opacity: 0;
  transition-duration: 500ms;
  transition-property: opacity, border-color;
}

.photos__name {
  font-size: 24px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.1;
  margin: 20px 0;
}

.photos__description {
  font-size: 12px;
  color: $descriptionColor;
  text-transform: uppercase;
  line-height: 1.667;
  margin: 20px 0;

  &::after {
    background-color: $white;
    width: 70px;
    height: 2px;
    display: block;
    content: '';
    margin-top: 20px;
  }

}

.photos__zoom, .photos__link {
  border-radius: 50%;
  background-color: #fff;
  position: relative;
  cursor: pointer;
  display: inline-block;
  width: 42px;
  height: 42px;
  margin: 0 2px;
  appearance: none;
  padding: 0;
  border: none;
  transition-property: background-color;
  transition-duration: 500ms;

  &:hover, &:active {
    background-color: $blue;
  }

  svg {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    fill: $white;
    width: 15px;
    height: 15px;
  }

}

.photos__btns {
  position: absolute;
  right: 30px;
  bottom: 30px;
}

.photos__element--active {

  &::before {
    z-index: 1;
    opacity: 1;
  }

  .photos__info {
    border-color: $second;
    opacity: 1;
    z-index: 2;
  }

}


.photos__info__a {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@media (max-width: 980px) {

  .photos__title {
    font-size: 24px;
  }

  .photos__name {
    font-size: 18px;
  }

}

@media (max-width: 958px) {

  .photos__list {
    grid-template-columns: 1fr;
  }

  .photos__element {
    grid-column: span 1;

    &:first-child {
      grid-column: 1 / -1;
      max-width: 100%;
    }

  }

  p.photos__text a button.services__button {
    margin: unset;
  }
}

@media (max-width: 768px) {
  .photos__wrap{
    grid-template-columns: 1fr;
    padding: 0;
  }


  .photos__left__first .circle {
    margin: 0 auto;
  }

  .photos__left__second {
    position: relative;
    writing-mode: unset;
    transform: unset;
    text-align: center;
  }

  .photos__wrap {
    padding-top: 10px;
    grid-gap: 0;
  }

  .photos__list {
    padding-bottom: 0;
  }
}

@media (max-width: 512px) {
  .photos__wrap {
    grid-template-columns: 1fr;
    background-attachment: unset;
  }

  .photos__list {
    grid-template-columns: minmax(0, auto);
    padding-top: 40px;
    padding-bottom: 80px;
  }

}
