$text: #fff;
$svgDefault: #fff;
$onHover: #eef6ff;


.send__mail {
  background: url('/images/home/send_mail.jpg') no-repeat center center;
  position: relative;
  //background-attachment: fixed !important;
  display: block;
  background-size: cover;
  padding-bottom: 30px;
  padding-top: 30px;
  margin-bottom: 30px;

  &::before {
  @include color-cover();
  }
}



.send__mail__header {
  align-self: center;
  justify-self: center;
  margin-right: 20px;
}

.send__mail__header__top {
  display: block;
  font-size: 18px;
  margin-bottom: 15px;
  font-weight: 800;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
  color: $text;
}

.send__mail__header__bottom {
  font-size: 32px;
  font-weight: 800;
  text-transform: uppercase;
  margin-top: 18%;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
  line-height: 1.2;
  color: $text;
}

.send__mail__list {
  position: relative;
  color: #fff;
  list-style: none;
  justify-items: center;
  text-align: center;
  display: grid;
  grid-row-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(0px, auto));
  grid-template-areas:
                        'send__mail__header send__mail__header'
                        'send__mail__phone send__mail__phone'
                        'send__mail__svg send__mail__form'
                        'send__mail__map send__mail__form'
}

.send__mail__header {
  grid-area: send__mail__header;
  font-size: 20px;
  font-weight: 800;
}

.send__mail__phone {
  grid-area: send__mail__phone;
  font-size: 26px;
  font-weight: 800;
}

.send__mail__svg {
  grid-area: send__mail__svg;
}


.messenger__img {
  border-color: $svgDefault;
  width: 52px;
  height: 52px;
  border-style: solid;
  border-width: 1px;
  border-radius: 50%;
  position: relative;
  transition-duration: 500ms;
  transition-property: border-color;
}

.messenger__svg {
  text-align: start;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 40px;
  fill: $grey;
  transform: translate(-50%, -50%);
  transition-duration: 500ms;
  transition-property: fill;
}

.send__mail__map {
  font-size: 20px;
  grid-area: send__mail__map;
}

.send__mail__form {
  grid-area: send__mail__form;
}

.send__mail__item {
  margin-right: 20px;
  font-size: 14px;
  line-height: 2em;
  letter-spacing: 1px;
  color: $text;
}

.send__mail__button {
  @include button($blue) {
    margin-top: 15px;
  };
}

.messenger__list {
  list-style: none;
  display: flex;
  justify-content: center;
}
.messenger__list li {
  padding: 20px;
}

.messenger__svg {
  fill: $svgDefault;
}

.messenger__subtitle {
color: $svgDefault;
}

.messenger__item{
  justify-self: center;

  &:hover {
    .messenger__img {
      border-color: $onHover;
    }

    .messenger__svg {
      fill: $onHover;
    }

    .messenger__subtitle{
      color: $onHover;
    }

    .messenger__text{
      color: $onHover;
    }
  }
}


form-control {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
}
button, input {
  overflow: visible;
}
button, input, optgroup, select, textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
input, button, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
input {
  line-height: normal;
}
button, input, optgroup, select, textarea {

  font: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  margin: 0;
  display: block;
  width: 100%;
}
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}

.form-group {
  margin-bottom: 15px;
}

@media (max-width: 768px) {

  li.send__mail__header > .messenger__list {
    grid-template-columns: auto;
    grid-column-gap: 10px;
  }
}
