.dog-view {
  padding-bottom: 50px;
  list-style: none;
  display: grid;
  grid-template-columns: minmax(0, 250px) minmax(0px, auto);
  grid-auto-rows: minmax(270px, auto);
  margin-top: 100px;
  grid-template-areas: 'sidebar-dog-view content-dog-view';
}

.sidebar-dog-view {
  grid-area: sidebar-dog-view;
}

.content-dog-view {
  grid-area:  content-dog-view;
}

.doginfo {
  max-width: 350px;
  margin: 0 auto;
}

.single-dog {
  border-top: 2px solid #69a5db;
  padding-top: 50px;
}


.carousel.slide {
  border: 2px solid #69a5db;
  background-color: #ecb9832b;
  padding-top: 20px;
}

div.single-dog img {
  border-radius: 20px;
  width: 100%;
}

.dog-information {
  margin: 20px;
  font-size: 24px;
  font-style: italic;
  letter-spacing: .022em;
  font-family: "Montserrat",sans-serif;
  color: #7f8da8;
  text-align: center;
}

div.doginfo.text-center a {
  display: block;
  text-align: center;
}

.dog-view-h2 {
  text-align: center;
  font-weight: 800;
  text-transform: uppercase;
  text-decoration: none;
  border: 3px solid rgba(113,154,183,.95);
  padding: 20px;
  font-size: 25px;
  color: #2b2b2b;
  letter-spacing: 1px;
  text-shadow: 2px 2px 2px rgba(41,41,41,.26);
  display: block;
}
.press-for-zoom{
  margin-top: 30px;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
}

 div.dog-information h2 {
   text-align: center;
   font-weight: 800;
 }

 .item img {
  max-width: 250px;
 }

@media(max-width: 768px) {

  .dog-view {
    grid-template-columns: minmax(0, auto);
    grid-template-areas: 'content-dog-view'
                         'sidebar-dog-view';
  }

}