li.services__item > a, li.about__item > a, p.photos__text a {
    text-decoration: none;
}

.navbar-toggle {
    background-color: #000;
}

a#scrollUp {
    bottom: 0px;
    right: 10px;
    padding: 5px 10px;
    background: #00a8e0;
    color: #FFF;
    -webkit-animation: bounce 2s ease infinite;
    animation: bounce 2s ease infinite;
}

a#scrollUp i{
    font-size: 30px;
}

//для предотвращение рывка при скролинге
.header__area {
    min-height: 100px;
}

/*actual or not actual*/
.pic img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainmenu ul li{
    padding-right: 15px;
    padding-left: 15px;
}

.mainmenu ul li:first-child{
    padding-left: 0px;
}

.mainmenu ul li a {
    color: #696763;
    font-family: 'Roboto', sans-serif;
    font-size: 17px;
    font-weight: 300;
    padding: 0;
    padding-bottom: 10px;
}

.mainmenu ul li a:hover, .mainmenu ul li a.active,  .shop-menu ul li a.active{
    background:none;
    color:#fdb45e;
}

.search_box input {
    background: #F0F0E9;
    border: medium none;
    color: #B2B2B2;
    font-family: 'roboto';
    font-size: 12px;
    font-weight: 300;
    height: 35px;
    outline: medium none;
    padding-left: 10px;
    width: 155px;
    background-image: url(../images/home/searchicon.png);
    background-repeat: no-repeat;
    background-position: 130px;
}


// ++++угловая мини-картинка (sold)
img.sold  {
    //position: absolute;
    //left: 68%;
    //top: 0%;
    transform: translate(-0%, -0%);
    width: 100%;
}

img.soldarrival {
    vertical-align: top;
    left: -95px;
    position: relative;
    top: 10px;
}

//for DOG/View
div.single-dog div.doginfo.text-center img.soldarrival{
    top: -260px;
    left: 92px;
