@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $black;
}

body {
	font-family: 'Roboto', 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	//@include min-max(320px, 100%, 0);
	background-color: $contentBackground;
    width: 100%;
	overflow-wrap: anywhere;
}

.container.admin {
	background-color: $white;
}

.wrap {
	@include min-max(320px, 1200px, 15px);
}

.circle {
	border-style: solid;
	border-width: 11px;
	border-color: #fff;
	border-radius: 50%;
	position: relative;
	width: 62px;
	height: 62px;
	transition-duration: 500ms;
	transition-property: border-color;
	transform: rotate(180deg);
	overflow-wrap: break-word;
}

.number__in__circle {
	font-size: 20px;
	position: absolute;
	left: 50%;
	top: 50%;
	fill: $grey;
	transform: translate(-50%, -50%) rotate(-180deg);
	transition-duration: 500ms;
	transition-property: fill;
}

.menuToggle{ color: #fff;
	padding: 10px 15px;
	cursor: pointer;
	display: none;
		background: url(/images/nav.png) left top no-repeat;
		height: 32px;
	margin-top: 10px;
}

.img-box {

}

@media(max-width: 640px){

	.menuToggle{
		display: block;
	}

	.menuNav{
		display: none;
	}

	.header__menu {
		display: flex;
		flex-direction: column;
	}

    li.menu__element {
		padding: 20px;
	}


}


