.about__wrap {
  background: url("/images/home/4.png");
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 200px) minmax(0, auto);
  padding: 150px 50px;
  grid-gap: 50px;
}

.about__wrap:after {
  position: absolute;
  content: '';
  left:0; top:0;
  width: 100%;
  height: 100%;
  background-color: rgba(99, 209, 170, 0.77);
}

.about__left {
  z-index: 1;
  font-size:48px;
  color: #fff;
  font-family: montserrat,sans-serif;
  font-weight: 400;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  text-transform: uppercase;
}

.about__left__first {
  margin-bottom: 40px;
  position: relative;
  z-index: 1;
}

.about__left__second {
  z-index: 1;
  position: relative;
  writing-mode: tb-rl;
  transform:rotate(180deg);
}


.about__right {
  color: #fff;
  position: relative;
  z-index: 1;
  font-family: roboto,sans-serif;

}

.about__right__title {
  font-size: 26px;
  font-family: montserrat,sans-serif;
  font-weight: 400;
  padding: 30px 0;
  margin-top: 80px;
  text-align: center;
}

.about__right__title:first-child {
  margin-top: 0;
}

.about__right__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
}



.about__header {
  align-self: center;
  justify-self: center;
  margin-right: 20px;
}

.about__header__top {
  display: block;
  font-size: 20px;
  margin-bottom: 15px;
  font-weight: 400;
  text-transform: uppercase;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
  color: $text;
}

.about__header__bottom {
  font-size: 32px;
  font-weight: 800;
  text-transform: uppercase;
  margin-top: 18%;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
  line-height: 1.2;
  color: $text;
}

.about__list {
  position: relative;
  color: $white;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, minmax(0px, auto));
  padding-bottom: 70px;
  text-align: center;
}

.about__item {
  margin-left: 22px;
  margin-right: 22px;
  font-size: 20px;
  line-height: 2em;
  letter-spacing: 1px;
  color: $text;
}

.about__item.description {
  background-color: rgba(155, 155, 155, 0.6);
  padding: 10px;
  text-align: center;
  border-radius: 70px;
  display: flex;
  align-items: center;
}

.phone__number {
  font-size: 33px;
  font-weight: 800;
  margin-top: 44px;
  display: block;
}

.about__button {
  @include button($blue) {
    margin-top: 15px;
  };
}
@media(max-width: 1200px) {

  .about__list {
    grid-template-columns: auto;
    display: block;
    padding: 0;
  }
}

@media(max-width: 768px) {


  .about__wrap {
    background-size: 100%;
    background-repeat: no-repeat;
    grid-template-columns: 1fr;
    padding: 10px;
    background-position: top right;
    background-attachment: unset;
  }

  .about__left__first .circle {
    margin: 0 auto;
  }

  .about__left__first .circle {
    margin: 0 auto;
  }

  .about__left__second {
    position: relative;
    writing-mode: unset;
    transform: unset;
    text-align: center;
  }

  .messenger__img svg.messenger__svg {
    width: 100%;
  }
}