$headerInfo: rgba(113, 154, 183, 0.8);
$headerMenuBackground: #fff;
$headerMenuBackgroundOnScrolling: #fff;
$headerMenuTextOnScrolling: #616161;



.main__page__header__wrap {
  max-width: 100%;
  position: fixed;
  margin-left: 0;
  margin-right: 0;
  padding-left: 0;
  z-index: 2;
  width: 100%;
}

