.main__page__header__top {
  background-color: $headerMenuBackground !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  top: 0;
  left: 0;
  transition: all 0.25s ease;
  width: 100%;
}

.main__page__header__info {
  text-align: center;
  max-width: 470px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 50px;
  padding-bottom: 40px;
  background-color: $headerInfo;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 2;
  letter-spacing: 4px;
  grid-row-start: 2;
}

.main__page__header__text {
  font-size: 12px;
  display: block;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 2.0;
}



@media (max-width: 896px) {

  .main__page__header__top {
    flex-direction: column;
    justify-content: flex-start;
  }

  .main__page__header__info {
    margin-top: 225px;
    max-width: 370px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
    padding-bottom: 10px;
    background-color: $headerInfo;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1;
    letter-spacing: 1px;
  }
}

@media(max-width: 1200px) {
 .main__page__header__top{
    justify-content: center;
  }
  div.common__header__top {
    justify-content: center;
  }
}
