$sliderBackground: #62c5e6;
$bannerText: black;
$commonText: #2b2b2b;
$bannerH1: #3e84ac;
$linkColor:  #1d7786;

.breed_category_content {
  //margin-top: 10px;
  //display: grid;
  //grid-template-columns: 250px auto;
  //grid-column-gap: 50px;
}

.breed__category__dogs__wrap {
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 60px;
  border: 4px solid #779eb9;
  background-image:url("/images/breed/1.jpg");
  background-color: #fff;
  background-size: cover;
  background-repeat: no-repeat;

  & img {
    width: 100%;
  }
}

.breed_category_dogs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0px, 350px));
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  justify-content: center;
}

div.carousel-top-title span h1 {
  all: unset;
}

div.productinfo.text-center h3 {
  all: unset;
}

section.breed__banner {
  //background-color: $sliderBackground;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 50px;
  display: grid;
  grid-template-columns: minmax(0px, 350px);
}

section.breed__banner h1 {
  font-size: 24px;
  text-transform: uppercase;
  color: $bannerH1;
}


section.breed__banner span {
  font-weight: 800;
  font-size: 18px;
  display: block;
}

section.breed__banner p {
  font-size: 16px;
}

.productinfo img{
  width: 100%;
}

.breed__left__sidebar__img {
  text-align: center;
}

.breed__left__sidebar__img img{
  width: 90%;
  padding-left:30px;
  text-align: center;
}

.breed__banner__item {
  color: $bannerText;
}

.item {
  //padding-left: 100px;
}


.item .carousel-top-title {
  color: #ffffff;
  font-family: abel;
  font-size: 38px;
  margin-top: 15px;
}

.item h1 span {
  color:#FE980F;
}

.item .carousel-bottom-title {
  color: $bannerText;
  font-family: 'Roboto', sans-serif;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 22px;
  margin-top: 10px;
}



.item  p {
  color: $bannerText;
  font-size:22px;
  font-weight:800;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.get {
  background: #00a8e0;
  border: 0 none;
  border-radius: 0;
  color: $bannerText;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  margin-top: 23px;
}


.doginfo p{
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #696763;
}

.doginfo h2{
  color: #FE980F;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 700;
}

.doginfo.text-center p a {
  font-size: 24px;
  font-weight: 800;
  text-align: center;
  color: $linkColor;
}

.breed__dog__link a{
  text-decoration: none;
}

.single-breed {
  position: relative;
  text-align: center;
}


.doginfo{
  position:relative;
}


.carousel-inner > .productinfo img {
  width: 50%;
}


.breed-left-sidebar {
  font-size: 26px;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
}

span.breed-left-sidebar {
  display: block;
  letter-spacing: .022em;
  font-family: "Montserrat",sans-serif;
  color: #779eb9;
}

ul.category-breed{
  padding-top: 50px;
  padding-left: 20px;
  font-size: 22px;
  color: $linkColor;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0px, 270px));
  grid-gap: 10px;
  justify-content: space-between;
  list-style: none;
  text-align: center;
}

.category-breed {
  border: 1px solid #F7F7F0;
  margin-bottom: 35px;
  padding-bottom: 20px;
  padding-top: 15px;
}


.category-breed ul{
  padding-left: 10px;
}
.category-breed li{
  display: inline-block;
  padding: 20px;
  border-top: 1px solid #779eb9;
  border-bottom: 1px solid #779eb9;
  transition-property: background-color;
  transition-duration: 500ms;

  &:hover {
    background-color: #d6dce4;
  }
}

.breed__dog__wrapper {
  border-radius: 20px;
  padding: 20px;
  background-color: #fff;
  -webkit-box-shadow: 4px 2px 8px 9px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 4px 2px 8px 9px rgba(34, 60, 80, 0.2);
  box-shadow: 4px 2px 8px 9px rgba(34, 60, 80, 0.2);
  transition-duration: 500ms;

  &:hover {
    transform: translateY(-20px);
  }

  & img {
    border-radius: 20px;
  }
}


.category-breed li a {
  color: $linkColor;
  transition-property: color;
  transition-duration: 500ms;
}

.category-breed .panel {
  background-color: #FFFFFF;
  border: 0px;
  border-radius: 0px;
  box-shadow:none;
  margin-bottom: 0px;
}

.category-breed .panel-default .panel-heading{
  background-color: #FFFFFF;
  border: 0 none;
  color: #FFFFFF;
  padding: 5px 20px;
}

.panel-group .panel-heading + .panel-collapse .panel-body {
  border-top: 0 none;
}

.category-breed .panel-default .badge,
.category-breed li .badge{
  background:none;
  border-radius: 10px;
  color: #696763;
  display: inline-block;
  font-size: 12px;
  font-weight: bold;
  line-height: 1;
  min-width: 10px;
  padding: 3px 7px;
  text-align: center;
  vertical-align: baseline;
  white-space: nowrap;
}

.breed__dog__wrapper__button {
  @include button($blue) {
    box-shadow: 0 25px 60px rgba(0, 0, 0, .2);
    width: unset;
  };
}

.breed__dog__wrapper__button__link {
  text-decoration: none;
}

.price {
  color: #23527c;
  text-decoration: underline;
  font-weight: 800;
  font-size: 22px;
  position: relative;
}

h2.title {
  color: #2b2b2b;
  font-family: 'Roboto', sans-serif;
  font-size: 26px;
  font-weight: 700;
  margin: 0 15px;
  text-transform: uppercase;
  margin-bottom: 30px;
  position: relative;
  text-align: center;
}

.owl-carousel.auto-scrolling  {
  width: 57% !important;
  margin: auto !important;
}

html body div.container section.tape div.owl-carousel.auto-scrolling.owl-theme.owl-loaded.owl-drag div.owl-dots button.owl-dot.active {
  width: unset;
}

html body div.container section.tape div.owl-carousel.auto-scrolling.owl-theme.owl-loaded.owl-drag div.owl-dots button.owl-dot {
  width: unset;
}

section.breed_category_content div.left-sidebar {
  padding-top: 100px;
  padding-right: 50px;
  padding-left: 50px;
}

.actual {
  background-color: #cce3cc;
}

@media (max-width: 1200px) {

  .breed__category__dogs__wrap {
      background-size: contain;
  }

  .breed_category_dogs  {
    grid-template-columns: minmax(0, 350px);
    justify-content: center;
  }
}

@media (max-width: 768px) {
  ul.category-breed {
    justify-content: center;
  }

  section.breed__banner {
    padding-left: 50px;
    padding-right: 50px;
    border-radius: 70px;
    background-color: #d2c7b0d1;
  }

  .breed_category_content {
    grid-template-columns: auto;
  }

  .category-breed {
    margin-bottom: 0;
  }

  ul.category-breed {
    padding-top: 0;
  }

  section.breed__banner h1 {
    font-size: 18px;
    text-align: center;
  }

  section.breed__banner span {
    text-align: center;
  }

  .breed__left__sidebar__img img {
    display: none;
  }

  .carousel-inner > .productinfo img {
    width: 10%;
  }
}



