 

/*************************
*******login page CSS******
**************************/

#form {
    display: block;
    margin-bottom: 185px;
    margin-top: 185px;
    overflow: hidden;
}

.login-form {

}

.signup-form {

}

.login-form h2, .signup-form h2 {
    color: #696763;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 300;
    margin-bottom: 30px;
}


.login-form form input, .signup-form form input {
    background: #F0F0E9;
    border: medium none;
    color: #696763;
    display: block;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 300;
    height: 40px;
    margin-bottom: 10px;
    outline: medium none;
    padding-left: 10px;
    width: 100%;
}

.login-form form span{
    line-height: 25px;
}

.login-form form span input {
    width: 15px;
    float: left;
    height: 15px;
    margin-right: 5px;
}

.login-form form button {
    margin-top: 23px;
}

.login-form form button, .signup-form form button {
    background: #00a8e0;
    border: medium none;
    border-radius: 0;
    color: #FFFFFF;
    display: block;
    font-family: 'Roboto', sans-serif;
    padding: 6px 25px;
}

.login-form label{

}


.login-form label input {
    border: medium none;
    display: inline-block;
    height: 0;
    margin-bottom: 0;
    outline: medium none;
    padding-left: 0;
}


.or{
    background: #00a8e0;
    border-radius: 40px;
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    height: 50px;
    line-height: 50px;
    margin-top: 75px;
    text-align: center;
    width: 50px;
}


.field-loginform-username.required label.col-lg-1.control-label, .field-loginform-password.required label.col-lg-1.control-label{
    color: #3b82b3 !important;
}

html body div.container div.site-login form#login-form.form-horizontal div.form-group.field-loginform-password.required.has-error div.col-lg-8 p.help-block.help-block-error, .field-loginform-username.required.has-error div.col-lg-8 p.help-block.help-block-error {
    color: red !important;
}

div.form-group.field-loginform-username.required,  input#loginform-password.form-control, input#loginform-rememberme, form#login-form.form-horizontal div.form-group div.col-lg-offset-1.col-lg-11 button.btn.btn-primary{
    max-width: 350px;
}

html body div.container div.site-login {
    text-align: center;
}


html body div.container div.site-login div{
    text-align: center;
    margin: 0 auto;
}

input#loginform-password.form-control {
    margin: 0 auto;
}

input#loginform-rememberme {
    margin: 0 auto;
}

.form-group div.col-lg-offset-1.col-lg-11 button.btn.btn-primary {
    margin: 10px auto;
}
