$headerInfo: #00a8e0;
$logoColor: #c0d5fd;
$headerMenuBackgroundOnScrolling: #fff;
$headerMenuTextOnScrolling: #616161;

.common__header__wrap {
  margin-left: 0;
  margin-right: 0;
  padding-left: 0;
  background-color: rgba(113,154,183,.95);
  background-size: cover;
  background-position: center center !important;
  color: $white;
  z-index: 1000;
}



.common__header__top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
}

.common__header__info {
  margin-top: 450px;
  max-width: 470px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 50px;
  padding-bottom: 40px;
  background-color: $headerInfo;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: 1px;
}

.common__header__text {
  font-size: 12px;
  display: block;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 2.0;
}

a.logo  {
    text-decoration: none;
}

div.common__header__top a.logo {
  transition-property: color;
  transition-duration: 500ms;

  &:hover, &:active {
    color: $logoColor;
  }
}

@media (max-width: 896px) {

  .common__header__top {
    flex-direction: column;
    justify-content: flex-start;
  }

  .features_items {
    padding-top: 30px;
  }

  .left-sidebar {
    text-align: center;
  }

  span.badge.pull-right {
    float: none !important;
  }

  .common__header__info {
    margin-top: 225px;
    max-width: 370px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
    padding-bottom: 10px;
    background-color: $headerInfo;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1;
    letter-spacing: 1px;
  }

  .common__header__top {
    padding-top: 0;
  }
}

@media(max-width: 768px) {
  .features_items {
    padding-top: 0;
  }
}