.intro__content {
  //padding: 130px 0 174px 0;
  position: absolute;
  bottom: 0;
  border-radius: 70px;
  background-color: #302e2ad1;
}

.intro__wrap {
  background: url("/images/home/1.png");
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  position: relative;
  height: 100vh;
}

.intro__wrap:after {
  position: absolute;
  content: '';
  left:0; top:0;
  width: 100%;
  height: 100%;
  background-color: rgba(97, 138, 175, 0.6);
}

.intro__content h1{
  margin: 50px;
  z-index: 1;
  color: #fff;
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 200;
  font-size: 50px;
  letter-spacing: 0.022em;
  text-transform: uppercase;
}

.intro__content p {
  z-index: 1;
  position: relative;
  margin: 50px;
  color: #d0cecc;
  font-size: 36px;
  line-height: 1.33333;
  letter-spacing: 0.022em;
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}

@media(max-width: 768px) {
  .intro__content {
  }

  .intro__content p {
    font-size: 20px;
  }

  .intro__content h1 {
    font-size: 24px;
  }

  .intro__wrap {
    background-size: 100%;
    background-repeat: no-repeat;
    grid-template-columns: 1fr;
    padding: 10px;
    background-position: top right;
    background-attachment: unset;
    height: 70vh;
  }
}