.cavalier__wrap {
    margin-bottom: 30px;
}

.cavalier_category_info {
    font-size: 18px;
    color: #7b7d7d;
    line-height: 2em;
    letter-spacing: 1px;
    font-family: 'Open Sans',sans-serif;
}

.cavalier__header__top {
    display: block;
    text-align: center;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 15px;
    color: #00a8e0;
    letter-spacing: 4px;
    text-shadow: 2px 2px 2px rgba(41, 41, 41, 0.26);
    font-family: 'Open Sans', sans-serif;
}

.cavalier__header__bottom {
    text-shadow: 2px 2px 2px rgba(41, 41, 41, 0.26);
    font-size: 25px;
    color: #2b2b2b;
    font-weight: 800;
    text-transform: uppercase;
    display: block;
    text-align: center;
    letter-spacing: 1px;
}

.cavalier__list {
    justify-content: center;
    list-style: none;
    display: grid;
    /*grid-gap: 35px;*/
    grid-template-columns: repeat(6, minmax(0, 300px));
    grid-template-rows: auto;

}

.cavalier__list img{
  width: 100%;
}

