/*VERTICAL align content center*/
.cavalier__item {
    align-self: center;
    justify-self: center;
}

.cavalier__subtitle {
    font-size: 14px;
    color: #444;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.6em;
    font-family: 'Open Sans', sans-serif;
    letter-spacing: 1px;

&::after {
     display: block;
     content: '';
     width: 50px;
     height: 2px;
     background-color: $blue;
     margin-top: 10px;
 }
}

.cavalier__text {
    font-size: 12px;
    color: #7b7d7d;
    line-height: 2em;
    letter-spacing: 1px;
    font-family: 'Open Sans', sans-serif;
}

.cavalier__button {
@include button($blue) {
    box-shadow: 0 25px 60px rgba(0, 0, 0, .2);
};
}

@media (max-width: 896px) {

    .cavalier__list {
        grid-template-columns: minmax(0, 350px);
        grid-template-rows: auto;
        grid-template-areas:
                'img_first'
                'img_second'
                'img_third'
                'img_fourth'
                'img_fifth'
                'img_sixth'
                'img_seventh'
                'img_eigth';


    }
}

.contact_category_info {
    font-size: 18px;
    color: #7b7d7d;
    line-height: 2em;
    letter-spacing: 1px;
    font-family: 'Open Sans',sans-serif;
    margin: 50px;
}
