$blue: rgba(113,154,183,.8);

.services__header__top {
  display: block;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 20px;
  color: #00a8e0;
  letter-spacing: 4px;
  text-shadow: 2px 2px 2px rgba(41, 41, 41, 0.26);
  font-family: 'Open Sans', sans-serif;
}

.services__header__bottom {
  text-shadow: 2px 2px 2px rgba(41, 41, 41, 0.26);
  font-size: 25px;
  color: #2b2b2b;
  font-weight: 800;
  text-transform: uppercase;
  display: block;
  text-align: center;
  letter-spacing: 1px;
}

.services__list {
  justify-content: center;
  list-style: none;
  display: grid;
  grid-gap: 35px;
  grid-template-columns: 350px 350px;
  grid-template-rows: auto;
  grid-template-areas:
          'desc_first img_first'
          'img_second desc_second'
          'desc_third img_third'
          'img_fourth desc_fourth';

}

.pic img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.serv__desc__first {
  grid-area: desc_first;
}

.serv__img__first {
  grid-area: img_first;
}

.serv__desc__second {
  grid-area: desc_second;
}

.serv__img__second {
  grid-area: img_second;
}

.serv__desc__third{
  grid-area: desc_third;
}

.serv__img__third {
  grid-area: img_third;
}

.serv__desc__fourth {
  grid-area: desc_fourth;
}

.serv__img__fourth {
  grid-area: img_fourth;
}

/*VERTICAL align content center*/
.services__item {
  align-self: center;
  justify-self: center;
}

.services__subtitle {
  font-size: 20px;
  line-height: 1.13333;
  color: #372d2d;
  text-transform: uppercase;

  &::after {
    display: block;
    content: '';
    width: 50px;
    height: 2px;
    background-color: $blue;
    margin-top: 10px;
  }
}

.services__text {
  font-family: PT Sans,Helvetica,Arial,sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.7;
  word-spacing: normal;
  letter-spacing: .01em;
  -webkit-text-size-adjust: none;
  color: #858585;
}

.services__button {
  @include button($blue) {
    box-shadow: 0 25px 60px rgba(0, 0, 0, .2);
  };
}

@media (max-width: 1200px) {
  .photos__text a button.services__button {
    width: 150px;
    font-size: 16px;
  }
}

@media (max-width: 896px) {

  .services__list {
    grid-template-columns: 350px;
    grid-template-rows: auto;
    grid-template-areas:
            'img_first'
            'desc_first'
            'img_second'
            'desc_second'
            'img_third'
            'desc_third'
            'img_fourth'
            'desc_fourth';
  }
}
@media (max-width: 786px) {
  .services__button {
    margin-bottom: 20px;
  }
}

@media (max-width: 512px) {
  .services__list {
    grid-gap: 0;
  }
}
@media (max-width: 512px) {
  .services__list {
    padding-left: 0;
    grid-template-columns: 1fr;
  }

  .services__button {
    width: 150px;
    font-size: 16px;
  }

}
