.tape img {
  margin-top: 20px;
  width: 35px;
}

/*clear default rule of owl-carousel */
.owl-theme .owl-nav {
  margin-top: 0;
}

html body div.container div.content section.tape div.owl-carousel.owl-theme.owl-loaded.owl-drag div.owl-dots button.owl-dot.active {
  width: unset !important;
}

html body div.container div.content section.tape div.owl-carousel.owl-theme.owl-loaded.owl-drag div.owl-dots button.owl-dot {
  width: unset !important;
}

section.tape div.owl-item.active div.item img {
  max-height: 265px;
  margin: 0 auto;
}

div.breed__category__dogs__wrap section.tape div.owl-carousel.owl-theme.breed-scroll.owl-loaded.owl-drag div.owl-stage-outer {
  overflow: unset;
}