$color: white;
$logo__blue: #6ba1ff;
$headerMenuBackgroundOnScrolling: #ececec;
$headerNav: black;
$headerNavHover: #00baff;

.menu {
  list-style: none;
}

ul.menu.header__menu li.menu__element a{
  color: $headerNav;
    text-decoration: none;
  transition-property: color;
  transition-duration: 500ms;

  &:hover {
    color: $headerNavHover;
  }

}

.menu__element {
  display: inline-block;
  margin-right: 20px;
  font-size: 24px;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;

  &:last-child {
    margin-right: 60px;
  }

  &:after {
    content: '';
    width: 100%;
    height: 2px;
    display: block;
    opacity: 0;
    background-color: $white;
    transition-property: opacity;
    transition-duration: 500ms
  }

  &:hover {


    &:after {
      opacity: 1;
    }
  }
}

.menu__element.active{
  display: inline-block;
  margin-right: 20px;
  font-size: 24px;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;

  &:after {
    content: '';
    width: 100%;
    height: 2px;
    display: block;
    opacity: 1;
    background-color: $white;
  }

}

.logo {
  margin-left: 40px;
  font-size: 35px;
  font-weight: 800;
  text-transform: uppercase;
  text-decoration: none;
  color: #98b4ca;
}

/*при скроллинге */
.sticky span.logo__blue {
  color: $logo__blue;
}

.sticky .menu__element.active {
  &:after {
    background-color: black;
  }
}

.sticky .menu__element {
  &:after {
    background-color: black;
  }
}
/* */


span.logo__blue {
  font-weight: 800;
  text-transform: uppercase;
  text-decoration: none;
  color: #96d2ca;
}

/*  Dropdown menu*/
.dropdown-menu  li  a:hover, .dropdown-menu  li  a:focus {
    background-color: #FE980F;
    color: #FFFFFF;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
}

.mainmenu ul li{
    padding-right: 15px;
    padding-left: 15px;
}

.mainmenu ul li:first-child{
    padding-left: 0px;
}

.mainmenu ul li a {
    color: #696763;
    font-family: 'Roboto', sans-serif;
    font-size: 17px;
    font-weight: 300;
    padding: 0;
    padding-bottom: 10px;
}

.mainmenu ul li a:hover, .mainmenu ul li a.active,  .shop-menu ul li a.active{
    background:none;
    color:#fdb45e;
}

.search_box input {
    background: #F0F0E9;
    border: medium none;
    color: #B2B2B2;
    font-family: 'roboto';
    font-size: 12px;
    font-weight: 300;
    height: 35px;
    outline: medium none;
    padding-left: 10px;
    width: 155px;
    background-image: url(../images/home/searchicon.png);
    background-repeat: no-repeat;
    background-position: 130px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  background-color: $headerMenuBackgroundOnScrolling !important;
}

.sticky a {
  color: $headerMenuTextOnScrolling !important;
}

.navbar-header
.navbar-toggle .icon-bar {
    background-color: #fff;
}


.nav.navbar-nav > li:hover > ul.sub-menu{
    display: block;
    -webkit-animation: fadeInUp 400ms;
    -moz-animation: fadeInUp 400ms;
    -ms-animation: fadeInUp 400ms;
    -o-animation: fadeInUp 400ms;
    animation: fadeInUp 400ms;
}

ul.sub-menu {
    position: absolute;
    top: 30px;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    list-style: none;
    padding: 0;
    margin: 0;
    width: 220px;
    -webkit-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
    display: none;
    z-index: 999;
}

.dropdown ul.sub-menu li .active{
    color: #FDB45E;
    padding-left: 0;
}


.navbar-nav li ul.sub-menu li{
    padding: 10px 20px 0 ;
}

.navbar-nav li ul.sub-menu li:last-child{
    padding-bottom: 20px;
}

.navbar-nav li ul.sub-menu li a{
    color: #fff;
}

.navbar-nav li ul.sub-menu li a:hover{
    color: #FDB45E;
}

.fa-angle-down{
    padding-left: 5px;
}

@-webkit-keyframes fadeInUp {
    0% {
        opacity: 0;
        -webkit-transform: translateY(20px);
        transform: translateY(20px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@media (max-width: 896px) {
  .menu__element {
    font-size: 14px;
  }

  .menu__element.active {
    font-size: 14px;
  }

  .logo {
    font-size: 22px;
  }
}
