
.contact__svg {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 40px;
  fill: $grey;
  transform: translate(-50%, -50%);
  transition-duration: 500ms;
  transition-property: fill;
}

label.control-label, div.form-group.field-contactform-name.required.has-success  {
  color: white !important;
}

html.mdl-js body div.container section.send__mail ul.send__mail__list.wrap li.send__mail__form form#w0 div.form-group.field-contactform-email.required.has-error label.control-label  {
  color: white !important;
}

.has-error .help-block, .has-error .control-label, .has-error .radio, .has-error .checkbox, .has-error .radio-inline, .has-error .checkbox-inline, .has-error.radio label, .has-error.checkbox label, .has-error.radio-inline label, .has-error.checkbox-inline label {
  color: white !important;
}
